.ProjectSelector {
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    display: block;
    background-color: #fafafa;
    border-top: 1px solid #f0f0f0;
    &__wrapper {
        box-sizing: border-box;
        padding: 0 20px;
        display: flex;
        align-items: center;
        min-height: 35px;
        font-size: 14px;
    }
}